import * as React from "react";
import ContentTitle from "../../typography/content-title";

export interface ParagraphType {
    title: string;
    texts: string[];
    img?: string;
}

const Paragraph = (paragraph: ParagraphType) => {
    return (
        <div className="paragraph block">
            <ContentTitle>{paragraph.title}</ContentTitle>
            {paragraph.texts.map((text, index) => <p key={"paragraph-" + index}>{text}</p>)}
            {paragraph.img ? <img src={__PATH_PREFIX__ + "/img/" + paragraph.img} alt={paragraph.img} /> : null}
        </div>
    );
};

export default Paragraph;



