import * as React from "react";
import Teammember from "./teamMember";
import SideTitle from "../../typography/side-title";
import {FullTeamMemberType} from "./teammember-types";

export interface TeamType {
    title: string;
    twitter: string;
    linkedin: string;
    teammembers: FullTeamMemberType[];
}

const Team = (teams: TeamType) => {
    return (
        <div>
            <SideTitle>{teams.title}</SideTitle>
            {teams.teammembers.map((team, index) => {
                return <Teammember member={team}
                                   twitter={teams.twitter}
                                   linkedin={teams.linkedin}
                                   key={"teammember" + index}/>;
            })
            }
        </div>
    );
};

export default Team;



