import * as React from "react";
import ContentTitle from "../../typography/content-title";
import {FullTeamMemberType} from "./teammember-types";

require("./team.scss");

const TeamMember: React.StatelessComponent<{member: FullTeamMemberType, twitter: string, linkedin: string} > =
    ({member, twitter, linkedin}) => {
    return (
        <div className="teammember">
            <div className="teammember-details">
                <div>
                    <ContentTitle>{member.name}</ContentTitle>
                    <p>{member.title}</p>
                </div>
                <ul>
                    <li><a href={"tel:" + member.phone}>{member.phone}</a></li>
                    <li><a href={"mailto:" + member.email}>{member.email}</a></li>
                    <li><a href={member.twitter}>{twitter}</a></li>
                    <li><a href={member.linkedin}>{linkedin}</a></li>
                </ul>
            </div>

            <div>{member.text}</div>
        </div>
    );
};

export default TeamMember;



