import * as React from "react";
import SideTitle from "../typography/side-title";
import {default as Team, TeamType} from "../components/team/team";
import Paragraph, {ParagraphType} from "../components/paragraphs/paragraphs";

require("./page.scss");

interface AboutPageProps {
    data: {
        site: {
            siteMetadata: {
                pages: {
                    about: {
                        title: string,
                        paragraphs: ParagraphType[],
                        teams: TeamType;
                    };
                };
            };
        };
    };
}

export default class extends React.Component<AboutPageProps, {}> {
    render() {
        const about = this.props.data.site.siteMetadata.pages.about;

        return (<div className="page">
                <article >
                    <SideTitle>{about.title} </SideTitle>
                    {
                        about.paragraphs.map((paragraph, index) => {
                            return <Paragraph {...paragraph} key={"paragraph-" + index}/>;
                        })
                    }
                </article>
                <article>
                    <Team { ...about.teams } />
                </article>
            </div>

        );
    }
}

/* tslint:disable */
export const aboutQuery = graphql`query AboutQuery {
 site {
      siteMetadata {
        pages {
          about {
            title,
            paragraphs {
              title
              texts
              img
            },
            teams {
                title,
                linkedin,
                twitter
                teammembers {
                  name
                  title
                  phone
                  email
                  twitter
                  linkedin
                  text
                }
            }
          }
        } 
      }
    }
  }
`;



